class Submenu extends React.Component {
    render() {
        return (
            <div className="submenu">
                <ul>
                    {this.props.data.map((menuitem, i) => (
                        <li key={i}><a href={menuitem.url}>{menuitem.title}</a></li>
                    ))}
                </ul>
            </div>
        )
    }
}

export default Submenu